
import { defineComponent } from 'vue';
import { MessageBox, Message } from '@/components';
import { Container } from './component';

export default defineComponent({
  name: 'MessageBoxExample',
  components: {
    Container,
  },
  setup() {
    const { alert: $alert, confirm: $confirm, prompt: $prompt } = MessageBox;
    const handleOpen = () => {
      $alert('这里是内容区域..', {
        confirmButtonText: '知道了',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen2 = () => {
      $confirm('此操作将永久删除该文件, 是否继续?', '提示信息', {
        iconType: 'warning',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen3 = () => {
      $prompt('请输入邮箱', {
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '请输入正确的邮箱号',
        inputPlaceholder: '请输入邮箱',
      }).then((value) => {
        Message.success(`您的邮箱是: ${value}`);
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen4 = () => {
      $confirm('此操作将永久删除该文件, 是否继续?', '提示信息', {
        iconType: 'warning',
        center: true,
        mask: false,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen5 = () => {
      $alert('<i>这是一个 <span style="color: orange;">HTML</span> 片段</i>', 'HTML 片段', {
        dangerouslyUseHTMLString: true,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen6 = () => {
      $confirm('检测到未保存的内容，是否在离开页面前保存修改？', '确认信息', {
        iconType: 'warning',
        distinguishCancelAndClose: true,
      }).then(() => {
        Message.success({
          content: '保存修改',
        });
      }).catch((action) => {
        Message.info(action === 'cancel' ? '放弃保存并离开页面' : '停留在当前页面');
      });
    };

    const handleOpen7 = () => {
      $confirm('此操作将永久删除该文件, 是否继续?', '提示信息', {
        iconType: 'warning',
        beforeClose(done, action) {
          if (action === 'confirm') {
            setTimeout(done, 2000);
          } else {
            done();
          }
        },
      }).then(() => {
        Message.success('文件删除成功！');
      }).catch(() => {
        Message.warning('取消删除文件！');
      });
    };

    return {
      handleOpen,
      handleOpen2,
      handleOpen3,
      handleOpen4,
      handleOpen5,
      handleOpen6,
      handleOpen7,
    };
  },
});
