
import { defineComponent, ref } from 'vue';
import { CheckBox } from '@/components';
import { Container, Attributes } from './component';

export default defineComponent({
  name: 'CheckboxExample',
  components: {
    Container,
    Attributes,
    KiCheckBox: CheckBox,
    KiCheckBoxGroup: CheckBox.Group,
  },
  setup() {
    const checked = ref(true);
    const checked1 = ref(true);
    const checked2 = ref(false);
    const allChecked = ref(false);
    const hobby = ref(['1']);
    const hobby1 = ref(['篮球']);

    const handleAllChecked = (isChecked: boolean) => {
      if (isChecked) {
        hobby.value = ['1', '2', '3'];
      } else {
        hobby.value = [];
      }
    };
    const handleHobbyChange = (value: (string | number)[]) => {
      allChecked.value = value.length === 3;
    };

    return {
      checked,
      checked1,
      checked2,
      allChecked,
      hobby,
      hobby1,
      handleAllChecked,
      handleHobbyChange,
      attributes: [
        {
          attribute: 'v-model',
          description: 'checkbox 绑定值',
          type: 'string | number | boolean',
          option: '——',
          default: '——',
        },
        {
          attribute: 'label',
          description: '选中状态的值，在 group 下该值会被添加到 group 绑定的数组中',
          type: 'string | number',
          option: '——',
          default: '——',
        },
        {
          attribute: 'disabled',
          description: '是否禁用',
          type: 'boolean',
          option: 'true | false',
          default: 'false',
        },
        {
          attribute: 'indeterminate',
          description: '设置 indeterminate 状态，只负责样式控制',
          type: 'boolean',
          option: 'true | false',
          default: 'false',
        },
      ],
      checkboxEvents: [
        {
          name: 'change',
          description: '发生变化时调用',
          type: '(value: boolean) => void',
        },
      ],
      groupAttributes: [
        {
          attribute: 'v-model',
          description: 'checkbox-group 绑定值',
          type: '(string | number)[]',
          option: '——',
          default: '——',
        },
      ],
      checkboxGroupEvents: [
        {
          name: 'change',
          description: '发生变化时调用',
          type: '(value: (string | number)[]) => void',
        },
      ],
    };
  },
});
